.newsCardWrapper{
    /* margin: 20px; */
    margin-bottom: 20px;
    background-color: #bdbdbd ;
    /* width: 9.375rem;
    height: 12.5rem; */
    border: 0.0625rem solid rgba(0,0,0,0.2);
    border-radius: 5px;
    box-shadow: 0.1875rem 0.125rem 0.625rem rgba(0,0,0,0.1);
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: stretch; 
    transition: 0.3s;
}
.newsCardWrapper:hover{
    transform: scale(1.02);
    box-shadow: 0.3rem 0.3rem 0.9rem rgba(0,0,0,0.1);
}

.newsCardText{
    font-size: 18px;
    padding-right: 20px;
    padding-left: 20px;
    color:black;
}